/* bmm styles */
.bmm-menu {
  width: 100%;
  position: relative;
}

.bmm-menu:after {
  content: '';
  display: table;
  clear: both;
}

.bmm-menu,
.bmm-sub-menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.bmm-primary-nav-item {
  float: left;
}

.bmm-primary-nav-item .bmm-menu-link {
  padding: 5px 10px;
  display: inline-block;
  width: 100%;
}

.bmm-primary-nav-item > .bmm-sub-menu {
  position: absolute;
  width: 100%;
  z-index: 999;
  left: 0;
  background: #fff;
  padding-top: 0.5rem;
}

.bmm-menu,
.bmm-sub-menu,
.row-menu-item,
.column-menu-item {
  display: block;
  float: none;
  height: auto;
  left: auto;
  margin: 0;
  min-height: auto;
  max-height: none;
  overflow: visible;
  padding: 0;
  position: relative;
  right: auto;
  text-align: left;
  top: auto;
  width: auto;
}

.bmm-menu {
  /* transitions so that menus display for a bit after hover off */
}

.bmm-menu .row-menu-item {
  width: 100%;
  float: left;
}

.bmm-menu .column-menu-item {
  position: relative;
  width: 100%;
  float: left;
  padding-right: 2%;
}

.bmm-menu .column-menu-item.total-columns-2 {
  width: 49%;
}

.bmm-menu .column-menu-item.total-columns-3 {
  width: 32%;
}

.bmm-menu .column-menu-item.total-columns-4 {
  width: 23.5%;
}

.bmm-menu .column-menu-item.total-columns-5 {
  width: 18.4%;
}

.bmm-menu .column-menu-item.total-columns-6 {
  width: 15%;
}

.bmm-menu .column-menu-item.total-columns-7 {
  width: 12.571428%;
}

.bmm-menu .column-menu-item.total-columns-8 {
  width: 10.75%;
}

.bmm-menu .column-menu-item:last-child {
  padding-right: 0;
}

.bmm-menu .menu-item.bmm-primary-nav-item > .bmm-sub-menu {
  visibility: hidden;
  opacity: 0;
  -webkit-transition: visibility 0s linear 200ms, opacity 200ms;
  transition: visibility 0s linear 200ms, opacity 200ms;
}

.bmm-menu .menu-item:hover > .bmm-sub-menu {
  visibility: visible;
  opacity: 1;
  -webkit-transition: visibility 0s linear 0s, opacity 200ms;
  transition: visibility 0s linear 0s, opacity 200ms;
}

.bmm-menu .menu-item .bmm-icon:before {
  display: table;
}

.bmm-menu .menu-item:hover .bmm-icon-wrapper {
  -webkit-transform: translateY(-8px);
          transform: translateY(-8px);
}

.bmm-menu .menu-item .bmm-icon-wrapper {
  background-color: red;
  text-align: center;
  border-radius: 50%;
  padding: 50% 0;
  height: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transition: -webkit-transform ease-in-out 0.25s;
  transition: -webkit-transform ease-in-out 0.25s;
  transition: transform ease-in-out 0.25s;
  transition: transform ease-in-out 0.25s, -webkit-transform ease-in-out 0.25s;
  /*


        .pmm-menu-icon-wrapper {
            


*/
}

.bmm-menu .menu-item .bmm-icon-wrapper .bmm-icon {
  font-size: 3rem;
}

.bmm-menu .menu-item .bmm-icon-wrapper .bmm-icon:before {
  display: inherit;
}

.bmm-menu .bmm-tabpane {
  visibility: hidden;
  width: 100%;
  text-align: left;
  opacity: 0;
  -webkit-transition-delay: 0.4s;
          transition-delay: 0.4s;
}

